const aboutTitle = document.querySelector('.aboutTitle');
const projectsTitle = document.querySelector('.projectsTitle');
const moreTitle = document.querySelector('.moreTitle');
const aboutSection = document.querySelector('.about');
const projectsSection = document.querySelector('.projects');
const moreSection = document.querySelector('.more');
var state = "";

const projectsItem = document.querySelectorAll('.projectsNav__title');
const projectsNav = document.querySelector('.projectsNav');

// functions to add/remove class '--is-open'
function classAddRemove() {
  projectsSection.classList.add('--is-open');
  moreSection.classList.remove('--is-open');
}
function classRemoveRemove() {
  projectsSection.classList.remove('--is-open');
  moreSection.classList.remove('--is-open');
}
function classAddAdd() {
  projectsSection.classList.add('--is-open');
  moreSection.classList.add('--is-open');
}

aboutTitle.addEventListener('click', function () {
  state = "about";
  classRemoveRemove(); 
});

projectsTitle.addEventListener('click', function () {
if (state !== "project"){
  state = "project";
  classAddRemove();
} else {
  state = "about";
  classRemoveRemove();
}
});

moreTitle.addEventListener('click', function () {
  if (state !== "more"){
    state="more";
    classAddAdd();
  } else {
    state = "project";
    classAddRemove();
  }
});

for (let i = 0; i < projectsItem.length; i++) {
  projectsItem[i].addEventListener('mouseover', function () {
    projectsNav.classList.add('--is-active');
  });
  projectsItem[i].addEventListener('mouseout', function () {
    projectsNav.classList.remove('--is-active');
  });
}